<template>
  <el-row class="warp" v-loading="loading" v-if="$route.name == ''">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main">
      <el-form ref="form" :model="form" label-width="150px">
        <el-form-item label="">
          <el-input v-model="form.user_name"  placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.user_mobile" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-select v-model="dep.uuid1" placeholder="" v-loading="loading">
            <el-option v-for="item in dep.option1" :label="item.org_name" :value="item.org_uuid" :key="item.org_uuid" value-key="id"></el-option>
          </el-select>

          <el-select v-model="dep.uuid2" placeholder="" v-if="dep.option2.length > 0 && dep.uuid1" v-loading="loading">
            <el-option v-for="item in dep.option2" :label="item.org_name" :value="item.org_uuid" :key="item.org_uuid" value-key="id"></el-option>
          </el-select>

          <el-select v-model="dep.uuid3" placeholder=""  v-if="dep.option3.length > 0 && dep.uuid2"  v-loading="loading">
            <el-option v-for="item in dep.option3" :label="item.org_name" :value="item.org_uuid" :key="item.org_uuid" value-key="id"></el-option>
          </el-select>

          <el-select v-model="dep.uuid4" placeholder=""  v-if="dep.option4.length > 0 && dep.uuid3" v-loading="loading">
            <el-option v-for="item in dep.option4" :label="item.org_name" :value="item.org_uuid" :key="item.org_uuid" value-key="id"></el-option>
          </el-select>

          <el-select v-model="dep.uuid5" placeholder=""  v-if="dep.option5.length > 0 && dep.uuid4" v-loading="loading">
            <el-option v-for="item in dep.option5" :label="item.org_name" :value="item.org_uuid" :key="item.org_uuid" value-key="id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.level_type" placeholder="">
            <el-option v-for="item in state"  :label="item.label" :value="item.value" :key="item.value" value-key="id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.manager_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.manager_mobile" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-date-picker
            v-model="form.times"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            @change="dataChange">
          </el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" icon="" @click="emptyFun"></el-button>
          <el-button type="primary" icon="el-icon-search" @click="search"></el-button>
        </el-form-item>
      </el-form>

      <el-table
        ref="multipleTable"
        :data="content.data"
        tooltip-effect="dark"
        style="width: 100%;"
        @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          prop="user_name"
          label="">
        </el-table-column>
        <el-table-column
          prop="user_mobile"
          label="">
        </el-table-column>
        <el-table-column
          prop="community_name"
          label="">
        </el-table-column>
        <el-table-column
          prop="family_name"
          label=""
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="manager_name"
          label="">
        </el-table-column>
        <el-table-column
          prop="manager_mobile"
          label="">
        </el-table-column>
        <el-table-column
          prop="time_create"
          label="">
        </el-table-column>
        <el-table-column
          prop="level_value"
          label="">
        </el-table-column>
        <el-table-column label="" width="100">
          <template slot-scope="scope">
            <!--<el-button
              size="mini"
              plain
              @click="handleUnbind(scope.$index, scope.row)" v-if="scope.row.bind_state != ''"></el-button>  -->
            <el-button
              size="mini"
              type="warning"
              plain
              @click="handleDetail(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- <el-button @click="batchunbind" type="danger" plain  style="margin-top: 20px"></el-button> -->

      <div class="page">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="content.current_page"
          :page-size="content.per_page"
          layout="total, prev, pager, next, jumper"
          :total="content.total">
        </el-pagination>
      </div>
     <!--  <div style="margin-top: 60px">
        <el-button type="primary" icon="el-icon-download" @click="exportDatas"></el-button>
      </div> -->
      <!--  -->
      <!-- <el-dialog
        title="?"
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose" v-loading="loading">
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelBind"> </el-button>
          <el-button type="primary" @click="batchunbindSure"> </el-button>
        </span>
      </el-dialog> -->

    </el-col>
  </el-row>
  <el-row v-else="$route.name == 'OA'">
    <el-col>
      <transition name="fade" mode="out-in">
         <router-view></router-view>
      </transition>
    </el-col>
  </el-row>
</template>
<style type="text/css" scoped>
  .el-form > div{
    display: inline-block;
  }
  .page{
    float:right;
    margin-top:20px;
    margin-right:40px;
    font-size: 30px;
  }
  .el-table thead{
    /*border:1px solid #ebeef5 !important;*/
    font-weight: bold;
    color:#000 !important;
  }
</style>
<script>
  import { commentList , org } from '../api/api';
  export default {
   data() {
      return {
        loading:true,
        form: {
          manager_name:'',
          manager_mobile:'',
          org_uuid:'',
          user_name: '',
          user_mobile:'',
          level_type:'',
          evaluate_type:'',
          times:[],
        },
        state:[
           {label:'',value:""},
           {label:'',value:"0"},
           {label:'',value:"1"},
           {label:'',value:"2"},
           {label:'',value:"3"},
        ],
        multipleSelection: [],
        content:{
            current_page:1,
            data:[],
            total:0,
            per_page:0,
        },

        // 
        dep:{
          uuid1:'',
          uuid2:'',
          uuid3:'',
          uuid4:'',
          uuid5:'',

          option1:'',
          option2:'',
          option3:'',
          option4:'',
          option5:'',
        },

        // ids:[],//

        // dialogVisible: false,

        curRow:{},//
      }
    },
    methods:{
      emptyFun(){
        window.location.reload();
      },
      initPage(){
        var localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
        let param = {
            access_token:localRoutes.access_token,
            page:this.content.current_page,
            manager_name:this.form.manager_name,
            manager_mobile:this.form.manager_mobile,
            org_uuid:this.form.org_uuid,
            user_name:this.form.user_name,
            user_mobile:this.form.user_mobile,
            level_type:this.form.level_type,
            evaluate_type:this.form.evaluate_type,
            time_start:this.form.times[0],
            time_end:this.form.times[1],
        };
        commentList(param).then((res) => {
          this.loading = false;
          if(res.code == "0"){
            this.content = res.content;
          }else{
            this.$message.error(res.message);
          }

        });
      },
      search() {
          this.initPage();
      },
      org(obj,uuid){
        var localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
        let param = {
            access_token:localRoutes.access_token,
            org_uuid:this.dep[uuid],
        };
        org(param).then((res) => {
          if(res.code == "0"){
            this.loading = false;
            this.dep[obj] = res.content.result.data;
          }
        });
      },
      dataChange(val){
         if(val != null){
          this.form.times[0] = this.formatDate(val[0]);
          this.form.times[1] = this.formatDate(val[1]);
        }else{
          return;
        }
      },
      formatDate(strTime) {
        var date = new Date(strTime);
        return date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate();
      },
      handleDetail(index,row){
        this.$router.push({path:'/viewdetail', query:{id:row.id , type:"3"} , name:""});
      },
      // handleUnbind(index,row){
      //   this.dialogVisible = true;
      //   this.ids = row.id;
      // },
      batchunbind() {// 
        if(this.multipleSelection.length > 0){
          for(var i = 0;i<this.multipleSelection.length;i++){
            this.ids.push(this.multipleSelection[i].id);
          }
          this.dialogVisible = true;
        }else{
          this.$message.error("");
        }
      },
      batchunbindSure(){//
        this.unbind();
      },
      cancelBind(){ //
        this.dialogVisible = false;
        this.ids = [];
      },
      unbind(){//
        var localRoutes =JSON.parse(window.sessionStorage.getItem('user'));
        let param = {
          access_token:localRoutes.access_token,
          ids:this.ids.toString(),
        };
        oaUnbind(param).then((res) => {
          this.loading = false;
          if(res.code == "0"){
            this.dialogVisible = false;
            this.$message.success("");
            this.ids = [];
            window.location.reload();
          }else{
            this.$message.error(res.data);
          }
        });
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
        console.log(val);
      },
      handleCurrentChange(val) {
        this.content.current_page = val;
        this.initPage();
      },
      exportDatas(){
        this.$router.push({path:'/backend/oa/export',query:{access_token:JSON.parse(window.sessionStorage.getItem('user')).access_token, oa_username:this.form.oa_username , user_id:this.form.user_id , bind_state:this.form.bind_state , time_start:this.form.times[0] , time_end:this.form.times[1] , org_uuid:this.form.org_uuid , name:this.form.name , mobile:this.form.mobile}});
      },
      handleClose(done) {
        this.dialogVisible = false;
      }
    },
    computed:{
      uuid1() {
        return this.dep.uuid1;
      },
      uuid2() {
        return this.dep.uuid2;
      },
      uuid3(){
        return this.dep.uuid3;
      },
      uuid4(){
        return this.dep.uuid4;
      }
    },
    watch:{
      uuid1(){
        this.dep.uuid2 = '';
        this.dep.uuid3 = '';
        this.dep.uuid4 = '';
        this.dep.uuid5 = '';
        this.form.org_uuid = this.dep.uuid1;
        this.org("option2","uuid1");
      },
      uuid2(){
        this.dep.uuid3 = '';
        this.dep.uuid4 = '';
        this.dep.uuid5 = '';
        this.form.org_uuid = this.dep.uuid2;
        this.org("option3","uuid2");
      },
      uuid3(){
        this.dep.uuid4 = '';
        this.dep.uuid5 = '';
        this.form.org_uuid = this.dep.uuid3;
        this.org("option4","uuid3");
      },
      uuid4(){
        this.dep.uuid5 = '';
        this.form.org_uuid = this.dep.uuid4;
        this.org("option5","uuid4");
      },
      uuid5(){
        this.form.org_uuid = this.dep.uuid5;
        this.org("","uuid5");
      },

    },
    created() {
      this.initPage();
      this.org("option1");
    }
  }

</script>



