import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
//
import login from '@/components/login.vue'
// import Dashboard from '@/components/Dashboard'

import Oabindcolourlife from '@/components/Oabindcolourlife'
import Bindpropermanage from '@/components/Bindpropermanage'
import Communitycomments from '@/components/Communitycomments'
import LimitManage from '@/components/LimitManage'
import Viewdetail from '@/components/Viewdetail'

Vue.use(Router);

export default new Router({
  // mode:'history',
  routes: [{
      path: '/login',
      name: '',
      component: login,
      hidden: true,
    },{
      path: '/',
      name: 'home',
      component: Home,
      //redirect: '/oabindcolourlife',
      leaf: true, // 
      // menuShow: true,
      iconCls: 'iconfont icon-home', // class
      children: [
        {path: '/oabindcolourlife', component: Oabindcolourlife, name: '', menuShow: false}
      ]
    },
    {
      path: '/',
      component: Home,
      name: 'OA',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-users', // class
      children: [
        {path: '/oabindcolourlife', component: Oabindcolourlife, name: 'OA', menuShow: true,
        children:[{path:'/viewdetail', component: Viewdetail, name: 'OA'}]}
      ]
    },
    {
      path: '/',
      component: Home,
      name: '1',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-books',
      children: [
        {path: '/bindpropermanage', component: Bindpropermanage, name: '', menuShow: true,
        children:[{path:'/viewdetail', component: Viewdetail, name: ''}]},
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: true, // 
      iconCls: 'iconfont icon-books',
      children: [
          {path: '/communitycomments', component: Communitycomments,name: '', menuShow: true,
          children:[{path:'/viewdetail', component: Viewdetail, name: ''}]},
      ]
    },
    {
      path: '/',
      component: Home,
      name: '',
      menuShow: true,
      leaf: false,
      // hidden: true,
      iconCls: 'iconfont icon-users',
      children: [
        {path: '/limitManage', component: LimitManage, name: '', menuShow: true},
      ]
    },

    // {
    //   path: '/',
    //   component: Home,
    //   name: '',
    //   menuShow: true,
    //   iconCls: 'iconfont icon-setting1',
    //   children: [
    //     {path: '/user/profile', component: UserProfile, name: '', menuShow: true},
    //     {path: '/user/changepwd', component: UserChangePwd, name: '', menuShow: true}
    //   ]
    // }


  ]
})



